.NudoMap {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.SearchMap {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 30px;
}

.ComboboxInput {
  border: 1px solid grey;
  border-radius: 5px;
  font-size: 14px;
  padding: 7px 60px 7px 10px;
  margin-bottom: 16px;
}

textarea:focus,
input:focus {
  outline: none;
}

.ContainerMap {
  border-radius: 10px;
  box-shadow: 0px 1px 16px -2px rgba(0, 0, 0, 0.12);
  padding: 30px 30px;
  margin-bottom: 40px;
}

.RoundedMap {
  border-radius: 30px;
  overflow: hidden;
}
